*{
  margin: 0;
  padding: 0;
}

body{
  background-color: rgb(136, 243, 150);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.heading{
  text-align: center;
  color: black ;
  margin: 5px;
}
.app{
background: linear-gradient(90deg,pink,blue);
width:50vw;
min-height: 200px;
height: min-content;
border-radius: 15px;
padding: 20px;
box-shadow: 10px 10px 50px 0 black;
}

.Answer-Section{
  display: flex;
  flex-direction: column;
  margin: 10px;
  
}
.Question-Section{
  color: white; 
}
.Question-Section span{
  color: rgb(255, 0, 0); 
}
.Answer-Section button{
padding: 10px;
margin: 5px;
background-color: white;
border: 2px solid black;
border-radius: 10px;
color: white;
background-color: black;
cursor: pointer;
}

.Answer-Section button:hover{
  background-color: rgb(82, 78, 78);
}
.score-sec h1{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50vh;
}
.score-sec span{
color: rgb(255, 0, 0);  
font-size: 40px;
padding: 2px;
}
.current-question{
  color: black;
  font-size: 15px;
  font-weight: bolder;
}

